 input {
    margin: 5px;
    padding: 8px;
    width: 200px;
    border-radius: 5px;
    border: 1px solid #ccc;
  }

  button {
    margin-bottom: 10px;
    padding: 10px 15px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
button:hover {
    background-color: #0056b3;
  }